@use "src/variables.scss" as var;

.navBar {
    list-style-type: none;
    fill: rgb(0, 0, 0);
    font-size: 25px;
    width: 100px;
    position: fixed;
    top: 40%;
    left: 0;


    transform: scale(1.3);


    
    ul {
        list-style-type: none;
    }

    li {
        cursor: pointer;
        margin-bottom: 1rem;
        display: inline-block;
        margin: 10px;

        &.grow {
            transition: all .2s ease-in-out;
    
            &:hover {
              transform: scale(1.5);
            }
        }
    }

    a {
        text-decoration: none;
    }
}



@media screen and (max-width: var.$navbarSwap) {
    .navBar {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        left: calc(50% - 100px);
        padding-top: 15px;
        top: 10px;
        width: fit-content;
        height: auto;
        ul {
            list-style-type: none;
            margin: auto;
            padding: 0;
          }
          
          li {
            margin: auto;
          }
          
          li a {
            padding: 10px;
            text-decoration: none;
          }
    }
}