@use "src/variables.scss" as var;

.Collapsible {
    margin-bottom: 1rem;
    padding-top: 0.5rem;
    background-color: rgb(212, 212, 212);
}

.Collapsible__trigger {
    font-size: 20px;
    color: rgb(143, 143, 143);
    margin: 0.5rem;
    cursor: pointer;

    &.is-open {
        color: rgb(63, 63, 63);
    }
}

.Collapsible__contentOuter {
    margin-top: 0.5rem;
    background-color: rgb(185, 185, 185);
    box-shadow: inset 0px 26px 32px -30px #000000;
    width: 100%;
}

.Collapsible__contentInner {
    margin-top: 1rem;
}